module.exports = {"playingCards":[{"name":{"zh_tw":"Bang!","en_us":"Bang!"},"description":"被「Bang!」攻擊的玩家需要以有「射失」效果的牌抵銷，或被扣除 1 點生命值，在沒有裝備和特殊能力指定距離下，玩家只可攻擊距離為 1 的對手。"},{"name":{"zh_tw":"射失","en_us":"Missed!"},"description":"被「Bang!」效果攻擊時用作抵銷「Bang!」。"},{"name":{"zh_tw":"啤酒","en_us":"Beer"},"description":"回復 1 點生命值，在失去最後 1 點生命值時也可以使用。在剩餘兩人時無效果。"},{"name":{"zh_tw":"驚嚎","en_us":"Cat Balou"},"description":"棄掉任何一位玩家的隨機一張手牌或者面前的任一張牌。"},{"name":{"zh_tw":"恐慌","en_us":"Panic!"},"description":"從離自己距離 1 的一位玩家手上隨機抽去或面前中抽去一張牌加入手牌。"},{"name":{"zh_tw":"決鬥","en_us":"Duel"},"description":"與任一玩家決鬥，此時由被決鬥者丟出「Bang!」牌，再輪到決鬥者丟出「Bang!」牌，直至其中一方不能丟出「Bang!」牌時扣減 1 點生命值。"},{"name":{"zh_tw":"雜貨店","en_us":"General Store"},"description":"在牌庫取出每位生存玩家數量的牌並展示，再由使用者開始以行動順序每人選取一張做為自己的手牌。"},{"name":{"zh_tw":"印第安","en_us":"Indians"},"description":"所有玩家需丟棄一張「Bang!」牌，或被扣去 1 點生命值。"},{"name":{"zh_tw":"馬車","en_us":"Stagecoach"},"description":"從牌庫摸兩張手牌。"},{"name":{"zh_tw":"掃射","en_us":"Gatling"},"description":"除發出者以外每位玩家需丟棄一張有「射失」效果的牌，或被扣去 1 點生命值。"},{"name":{"zh_tw":"酒館","en_us":"Saloon"},"description":"玩家自己回復 1 點生命值並除玩家外所有人回復 1 點生命值，即全體回復 1 點生命值，但不能用作補充失去的最後 1 點生命。"},{"name":{"zh_tw":"開拓車","en_us":"Wells Fargo"},"description":"從牌庫摸三張手牌。"},{"name":{"zh_tw":"監獄","en_us":"Jail"},"description":"向警長以外的任一玩家裝備「監獄」，該玩家回合開始時需判定，若為紅心，「監獄」不會發揮作用，否則跳過該玩家的回合。判定結束後棄掉「監獄」。"},{"name":{"zh_tw":"斯科菲爾德式左輪槍","en_us":"Schofield"},"description":"裝備後攻擊距離最遠為 2。"},{"name":{"zh_tw":"酒桶","en_us":"Barrel"},"description":"裝備後被其他玩家以「Bang!」攻擊時，可以判定：若為紅心，則視為打出了一張「射失」（被Slab the Killer攻擊時仍需打出第二張「射失」牌 ）。"},{"name":{"zh_tw":"野馬","en_us":"Mustang"},"description":"所有玩家對裝備者的距離加 1，表示其他玩家需要以更遠距離的槍械才可用「Bang!」牌攻擊裝備者。"},{"name":{"zh_tw":"霰彈槍","en_us":"Volcanic"},"description":"攻擊距離 1，裝備者在一回合中可使用任意數量的「Bang!」牌。"},{"name":{"zh_tw":"阿帕盧薩馬","en_us":"Appaloosa"},"description":"裝備者對所有玩家的距離減 1，表示裝備者可以用「Bang!」牌攻擊更多目標。"},{"name":{"zh_tw":"炸藥","en_us":"Dynamite"},"description":"裝備後第一個回合沒有作用。在裝備者下一次行動前需先判定，如為黑桃♠2－9，炸藥爆炸並導致 3 個生命點損失，否則「炸藥」傳至下一位玩家面前，直至炸藥爆炸、「炸藥」可被人以「驚嚎」丟棄或被人以「恐慌」奪去。"},{"name":{"zh_tw":"雷明頓式左輪槍","en_us":"Remington"},"description":"裝備後攻擊距離最遠為 3。"},{"name":{"zh_tw":"轉輪式卡賓槍","en_us":"Rev. Carabine"},"description":"裝備後攻擊距離最遠為 4。"},{"name":{"zh_tw":"溫切斯特式步槍","en_us":"Winchester"},"description":"裝備後攻擊距離最遠為 5。"},{"name":{"zh_tw":"閃避","en_us":"Dodge"},"description":"與「射失」效果相同，但使用後可再摸一張牌。"},{"name":{"zh_tw":"打架","en_us":"Brawl"},"description":"與另一張牌一起使用，可「驚嚎」所有其他玩家。"},{"name":{"zh_tw":"拳頭","en_us":"Punch"},"description":"可「BANG!」距離 1 的一位玩家。"},{"name":{"zh_tw":"爵士樂","en_us":"Rag Time"},"description":"與另一張牌一起使用，可「恐慌」任何一位玩家而不限距離。"},{"name":{"zh_tw":"亂射","en_us":"Springfield"},"description":"與另一張牌一起使用，可「BANG!」任何一位玩家而不限距離。"},{"name":{"zh_tw":"龍舌蘭","en_us":"Tequila"},"description":"與另一張牌一起使用，可回復任何一位玩家 1 點生命，但不能用作補充失去的最後 1 點生命。"},{"name":{"zh_tw":"威士忌","en_us":"Whiskey"},"description":"與另一張手牌一起使用，可回復 2 點生命，但不能用作補充失去的最後 1 點生命值。"},{"name":{"zh_tw":"雙筒望遠鏡","en_us":""},"description":"與「阿帕盧薩馬」有相同功效，但可以同時裝備在面前。"},{"name":{"zh_tw":"巨石","en_us":"Hideout"},"description":"與「野馬」有相同功效，但可以同時裝備在面前。"},{"name":{"zh_tw":"鐵板","en_us":"Iron Plate"},"description":"有「射失」效果。"},{"name":{"zh_tw":"聖經","en_us":"Bible"},"description":"有「射失」效果，使用後摸一張牌。"},{"name":{"zh_tw":"來復槍","en_us":"Buffalo Rifle"},"description":"可「Bang!」任何一位玩家而不限距離。"},{"name":{"zh_tw":"坎坎舞","en_us":"Can Can"},"description":"與「驚嚎」相同。"},{"name":{"zh_tw":"水壺","en_us":"Canteen"},"description":"與「啤酒」相同，但兩人決鬥中仍可使用而且不能用作補充失去的最後 1 點生命值。"},{"name":{"zh_tw":"大篷車","en_us":"Conestoga"},"description":"可「恐慌」任何一人而不限距離。"},{"name":{"zh_tw":"德林加槍","en_us":"Derringer"},"description":"可「Bang!」距離 1 的一位玩家並摸一張牌。"},{"name":{"zh_tw":"大砲","en_us":"Howitzer"},"description":"與「掃射」相同。"},{"name":{"zh_tw":"飛刀","en_us":"Knife"},"description":"可「Bang!」距離 1 的一位玩家。"},{"name":{"zh_tw":"胡椒槍","en_us":"Pepperbox"},"description":"有「Bang!」效果。"},{"name":{"zh_tw":"驛馬","en_us":"Pony Express"},"description":"從牌庫摸三張牌。"},{"name":{"zh_tw":"墨西哥帽","en_us":"Sombrero"},"description":"有「射失」效果。"},{"name":{"zh_tw":"高呢帽","en_us":"Ten Gallon Hat"},"description":"有「射失」效果。"},{"name":{"zh_tw":"瞄準","en_us":"Aim"},"description":"將這張牌與另一張「Bang!」牌一起使用，如果目標玩家被打中，則該玩家損失兩點生命值。"},{"name":{"zh_tw":"走火","en_us":"Backfire"},"description":"此牌計算為一張「射失」牌，且同時攻擊你的玩家成為「Bang!」效果的目標。"},{"name":{"zh_tw":"強盜","en_us":"Bandido"},"description":"所有玩家選擇一項：棄兩張手牌（若只有一張則棄一張 ）或失去 1 點生命值。"},{"name":{"zh_tw":"逃命","en_us":"Escape"},"description":"此牌可以在回合外使用，無效一張棕邊牌（除了「Bang!」牌 ）對你產生的效果。"},{"name":{"zh_tw":"雙槍","en_us":"Fanning"},"description":"此牌計算為一張「Bang!」牌，攻擊的同時，選擇另外一位被你攻擊的玩家所看距離為 1 的玩家（除了你自己 ）做為同時攻擊的目標。"},{"name":{"zh_tw":"最後一杯","en_us":"Last Call"},"description":"回復 1 點生命值，但沒有「啤酒」的其他效果。"},{"name":{"zh_tw":"撲克","en_us":"Poker"},"description":"所有其他玩家同時棄掉一張手牌，如果這些牌中沒有數字 A，則你從這些牌中選擇至多兩張牌做為手牌。"},{"name":{"zh_tw":"救援","en_us":"Saved!"},"description":"此牌可以在回合外使用，防止另一位玩家損失 1 點生命值，若成功防止，則抽取該玩家兩張手牌或從牌庫摸兩張牌。"},{"name":{"zh_tw":"戰斧","en_us":"Tomahawk"},"description":"對距離2以內的玩家使用一次「Bang!」效果。"},{"name":{"zh_tw":"龍捲風","en_us":"Tornado"},"description":"每位玩家棄掉一張手牌（沒手牌則不用棄)，然後從牌庫摸兩張牌。"},{"name":{"zh_tw":"鬼魂","en_us":"Ghost"},"description":"裝備在一位已死亡的玩家面前，該玩家以 0 點生命值回到遊戲中並如同一般玩家進行遊戲，但無法回復或失去生命值。當「鬼魂」離開該玩家面前時（棄掉或抽走 ），則該玩家再次死亡。（可觸發獎懲或其他角色能力 ）"},{"name":{"zh_tw":"賞金","en_us":"Bounty"},"description":"裝備在任何玩家面前，若該玩家因為「Bang!」牌而失去生命值，則使用該「Bang!」牌的玩家從牌庫摸一張牌。"},{"name":{"zh_tw":"拉馬特式左輪槍","en_us":"LeMat"},"description":"攻擊距離 1，在你的回合，你可以將任何一張手牌當做「Bang!」牌使用。"},{"name":{"zh_tw":"響尾蛇","en_us":"Rattlesnake"},"description":"裝備在任何玩家面前，該玩家回合開始時判定：若為黑桃，該玩家失去 1 點生命值。"},{"name":{"zh_tw":"霰彈槍","en_us":"Shotgun"},"description":"攻擊距離 1，每當你讓其他玩家失去生命值，該受傷玩家需選擇一張手牌棄掉。"}]}

